import {Footer, Header} from './containers';
import {Navbar} from './components';
import {BrowserRouter} from 'react-router-dom';
import LinkRoutes from './LinkRoutes';
import './App.css';
const App = () => {
  return (
	<BrowserRouter>
		<div className="App">
			<div className = "gradient__bg">
				<Navbar />
				<LinkRoutes />
			</div>
			<Footer />
		</div>
	</BrowserRouter>
  )
}

export default App