import { About, Design, Illustration, Projects } from "./containers";
import { Routes, Route, useLocation } from "react-router-dom";
import { AnimatePresence } from "framer-motion";
import "./linkroutes.scss";

export default function LinkRoutes() {
  const location = useLocation();
  const componentMap = [
	{ key: 1, path: "", element: <About /> },
	{ key: 2, path: "game-development", element: <Projects /> },
	// {key: 3, path: 'design', element: <Design/>},
	{ key: 4, path: "illustration", element: <Illustration /> },
  ];

  return (
	<div className="portfolio__routes">
	  <AnimatePresence>
		<Routes location={location}>
		  {componentMap.map((item) => (
			<Route
			  key={item.key}
			  path={"portfolio/" + item.path}
			  element={item.element}
			/>
		  ))}
		</Routes>
	  </AnimatePresence>
	</div>
  );
}
