import './projects.scss';
import { useEffect, useState} from 'react';
import { gameDevCards, softwareCards } from '../../data';
import {HiChevronDown as Down, HiChevronUp as Up} from 'react-icons/hi';
import { Card } from '../../components';
import { motion } from 'framer-motion';
import { Slide } from '@mui/material';

export default function Projects() {
// const arrowRef = useRef(null);
// const handleClick = () => {
// 	arrowRef.current.scrollIntoView({ behavior: 'smooth' }); 
// };
const [tooManyGames, setTooManyGames] = useState(false);
const [tooManySoftware, setTooManySoftware] = useState(false);
useEffect(() => {if (gameDevCards.length > 4){ setTooManyGames(true) }}, [gameDevCards]);
useEffect(() => {if (softwareCards.length > 4){ setTooManySoftware(true) }}, [softwareCards]);

	return (
		<motion.div
		key="1"
		initial={{ opacity: 0 }}
		animate={{ opacity: 1 }}
		exit={{ opacity: 0, y: 100 }}
		>
			<div className='portfolio__projects'>
				<div className='portfolio__projects-container-desc'>
					<h1>Game Development</h1>
				</div>
				<div className='portfolio__projects-cards'>
					<div className='portfolio__projects-card-container'>
						{!tooManyGames && gameDevCards.map((item) => 
							<Card key = {item.key} image = {item.image} screenshots = {item.screenshots} title = {item.title} 
							link = {item.link} description = {item.description} github = {item.github}/>
						)}
						{tooManyGames && gameDevCards.slice(0, 4).map((item) =>
							<Card key = {item.key} image = {item.image} screenshots = {item.screenshots} title = {item.title}
							link = {item.link} description = {item.description} github = {item.github}/>
						)}
					</div>
				</div>
				{tooManyGames 
					? <p>Expand</p>
					: <p></p>
				}
				{tooManyGames 
					? <Down   color='#fff' size={50} style={{cursor: 'pointer'}} onClick={() => {setTooManyGames(false);}} />
					: <Up   color='#fff' size={50} style={{cursor: 'pointer'}} onClick={() => {setTooManyGames(true);}} />
				}
				<div className='portfolio__projects-container-desc'>
					<h1>Software Development</h1>
				</div>
				<div className='portfolio__projects-cards'>
					<div className='portfolio__projects-card-container'>
						{!tooManySoftware && softwareCards.map((item) => 
							<Card key = {item.key} image = {item.image} screenshots = {item.screenshots} title = {item.title} 
							link = {item.link} description = {item.description} github = {item.github}/>
						)}
						{tooManySoftware && softwareCards.slice(0, 4).map((item) =>
							<Card key = {item.key} image = {item.image} screenshots = {item.screenshots} title = {item.title} 
							link = {item.link} description = {item.description} github = {item.github}/>
						)}
						
					</div>
				</div>
				{tooManySoftware
					? <p>Expand</p>
					: <p></p>
				}
				{tooManySoftware
					? <Down color='#fff' size={50} style={{cursor: 'pointer'}} onClick={() => {setTooManySoftware(false)}} />
					: <Up color='#fff' size={50} style={{cursor: 'pointer'}} onClick={() => {setTooManySoftware(true)}} />
				}
			</div>
		</motion.div>
	)
}
