import './about.scss';
import {motion} from 'framer-motion';
const Icons = () => (
    <>
        <a href="https://github.com/dsol-cpu"><img src={require('../../assets/images/icon-github.png')} alt="" /></a>
        <a href="https://www.linkedin.com/in/david-solinsky/"><img src={require('../../assets/images/icon-linkedin.png')} alt="" /></a>
        <a href="https://www.upwork.com/freelancers/~0179a1a2a2df5212fd"><img src={require('../../assets/images/icon-upwork.png')} alt="" /></a>
        <a href="https://www.instagram.com/smep_itty/"><img src={require('../../assets/images/icon-instagram.png')} alt="" /></a>
    </>
)

export default function About() {


  return (
	<motion.div
	key="4"
	initial={{ opacity: 0 }}
	animate={{ opacity: 1 }}
	exit={{ opacity: 0, y: 100 }}
	>
		<div className='portfolio__about'>
			<div className='portfolio__about-container'>
				<div className='portfolio__about-container-frame'>
					<img src={require('../../assets/images/me.png')} alt="" />
				</div>
				<div className='portfolio__about-container-textbox'>
					<p> I'm a Software Engineer, Game Developer, and Artist based in Herndon, Virginia.</p>
					<p> I graduated from the College of William and Mary with a Bachelor's of Science in Computer Science.</p>
				</div>
			</div>
		</div>
	</motion.div>
  )
}
