import {useState} from 'react';
import {RiMenu3Line, RiCloseLine} from 'react-icons/ri'
import {Link} from 'react-router-dom'
import Resume_PDF from '../../assets/documents/resume.pdf';

import './navbar.scss';
const titles = {
	'/portfolio/': 0,
	// '/portfolio/design': 1,
	'/portfolio/illustration': 2,
	'/portfolio/prowork': 3,
	'/portfolio/projects': 4,
};


export default function Navbar() {
	const [toggleMenu, setToggleMenu] = useState(false);

	const Menu = () => (
		<>
			<p><Link to="portfolio/">Home</Link></p>
			<p><Link to="portfolio/game-development">Game Development</Link></p>
			{/* <p><Link to="portfolio/design">Character Design</Link></p> */}
			<p><Link to="portfolio/illustration">Illustration</Link></p>
			<p><a href={Resume_PDF} target="_blank">Resume</a></p>
		</>
)

	return (
		<div className='portfolio__navbar'>
			<div className='portfolio__navbar-links_logo'>
					<h1> David Solinsky </h1>
					<h2>Software Engineer | Game Developer | Artist</h2>
			</div>
			<div className="portfolio__navbar-links">
				<div className='portfolio__navbar-links_container'>
					<Menu />
				</div>
			</div>
			<div className='portfolio__navbar-menu'>
				{toggleMenu 
					? <RiCloseLine color='#fff' size={27} onClick={() => setToggleMenu(false)} />
					: <RiMenu3Line color='#fff' size={27} onClick={() => setToggleMenu(true)} />
				}
				{toggleMenu && (
					<div className='portfolio__navbar-menu_container scale-up-center'>
						<div className='portfolio__navbar-menu_container-links'>
							<Menu />
						</div>
					</div>
				)}
			</div>
		</div>
	)
}