import { motion } from 'framer-motion';
import { illustrationImg, commissionImg } from '../../data';
import { IMG_Carousel } from '../../components';
import './illustration.scss';

export default function Illustration() {
	return (
		<motion.div
		key="6"
		initial={{ opacity: 0 }}
		animate={{ opacity: 1 }}
		exit={{ opacity: 0, y: 100 }}
		>
			<div className='portfolio__illustration'>
				<div className='portfolio__illustration-container-desc'>
					<h1>Illustrations</h1>
				</div>
				<div className='portfolio__illustration-container'>
					<div className='portfolio__illustration-container-frame'>
						{illustrationImg.map((item) =>
							<img key={item.key} src={require(`../../assets/images/${item.path}`)}/>
						)}
					</div>
				</div>
				<h1>Commissions</h1>
				<div className='portfolio__illustration-container'>
					<div className='portfolio__illustration-container-frame'>
						{commissionImg.map((item) =>
							<img key={item.key} src={require(`../../assets/images/${item.path}`)}
							alt='design' />
						)}
					</div>
				</div>
			</div>
		</motion.div>
	)
}
