import "./card.scss";
import React from "react";
import { useState } from "react";
import ellipsis from "../../assets/images/icon-ellipsis.png";
import github_icon from "../../assets/images/icon-github.png";
import { Modal, Paper, Slide } from "@mui/material";

export default function Card({
  image,
  screenshots,
  title,
  description,
  github,
}) {
  const [isOpen, setIsOpen] = useState(false);
  function handleClick() {
	setIsOpen(!isOpen);
  }
  return (
	<div className="portfolio__card">
	  <div className="portfolio__card-container">
		<div className="portfolio__card-frame">
		  {image && (
			<img src={require(`../../assets/images/${image}`)} alt={title} />
		  )}
		</div>
		<h1>{title}</h1>
		<div className="portfolio__card-row">
		  {
			<img
			  src={ellipsis}
			  style={{
				width: "100px",
				height: "100px",
				bottom: "fixed",
				cursor: "pointer",
			  }}
			  alt=""
			  onClick={handleClick}
			/>
		  }
		  {github && (
			<a href={github} target="_blank" rel="noreferrer">
			  <img src={github_icon} alt="" style={{ width: "50px" }} />
			</a>
		  )}
		</div>
	  </div>
	  {isOpen && (
		<Modal
		  open={isOpen}
		  onClose={handleClick}
		  aria-labelledby="modal-modal-title"
		  keepMounted
		  style={{
			display: "flex",
			alignItems: "center",
			justifyContent: "center",
			maxHeight: "100vh",
			overflowY: "auto",
		  }}
		>
		  <Slide
			direction="down"
			in={isOpen}
			mountOnEnter
			unmountOnExit
			easing={{
			  easeInOut: "cubic-bezier(0.4, 0, 0.2, 1)",
			  duration: "0.5s",
			}}
		  >
			<Paper
			  style={{
				position: "relative",
				margin: "auto",
				flexDirection: "nowrap",
				backgroundColor: "var(--color-blog)",
				borderRadius: "25px",
				overflow: "hidden",
				padding: "20px",
				outline: "none",
			  }}
			>
			  <div className="portfolio__card-modal">
				<div className="portfolio__card-modal-container">
				  <div className="portfolio__card-modal-frame">
					{image && (
					  <img
						src={require(`../../assets/images/${image}`)}
						alt={title}
					  />
					)}
				  </div>
				  <div className="portfolio__card-modal-container-body">
					<h1>{title}</h1>
					<p>{description}</p>
				  </div>
				  <div className="portfolio__card-modal-container-grid">
					{screenshots.map((item) => (
					  <img
						key={item.key}
						src={require(`../../assets/images/${item}`)}
					  />
					))}
				  </div>
				</div>
			  </div>
			</Paper>
		  </Slide>
		</Modal>
	  )}
	</div>
  );
}
